'use client';
import { sampleMovies } from '@/config/mock';
import { StreamVibeApp } from '../components/sections/AbouSection';
import { HeroSlider } from '../components/sliders/HeroSlider';
import CategorySection from '../components/sections/CategorySection';
import CallToAction from '../layouts/CallToAction';
import { useEffect, useState } from 'react';

export default function Page() {
  const [dataFromBackend, setDataFromBackend] = useState<any>(null); // ◊ê◊ï ◊ò◊ô◊§◊ï◊° ◊°◊§◊¶◊ô◊§◊ô ◊ê◊ù ◊ê◊™◊î ◊ô◊ï◊ì◊¢ ◊ê◊ï◊™◊ï
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null); // ◊©◊ô◊†◊ï◊ô ◊î◊ò◊ô◊§◊ï◊° ◊õ◊ê◊ü

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('/api/browse');
        if (!response.ok) {
          console.error('Failed to fetch data from backend');
          setError('Failed to fetch data');
          setLoading(false);
          return;
        }
        const data = await response.json();
        setDataFromBackend(data);
        setLoading(false);
        console.log('Data from backend:', data);
      } catch (error: any) {
        // ◊î◊ï◊°◊§◊™ ◊ò◊ô◊§◊ï◊° ◊ú◊©◊í◊ô◊ê◊î
        console.error('Error fetching data from backend:', error);
        setError('Error fetching data');
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <div>◊ò◊ï◊¢◊ü ◊†◊™◊ï◊†◊ô◊ù...</div>;
  }

  if (error) {
    return <div>◊©◊í◊ô◊ê◊î ◊ë◊ò◊¢◊ô◊†◊™ ◊î◊†◊™◊ï◊†◊ô◊ù: {error}</div>;
  }

  return (
    <div className="">
      <HeroSlider movies={sampleMovies} />
      <StreamVibeApp />
      <CategorySection />
      <CallToAction />
      {dataFromBackend && <pre>{JSON.stringify(dataFromBackend, null, 2)}</pre>}
    </div>
  );
}
